export class ZT {
  constructor() {
    this.Tabela = [];
    this.Rozwiazanie = [];
    this.Koszty = [];
    this.Popyt = [];
    this.Podaz = [];
    this.kosztOptymalny = 0;
    this.Optymalne = false;
    this.Cykl = [];
    this.cyklRoz = false;
    this.next = 1;
    this.return = 0;
    this.nieBazy = { X: [], Y: [] };
    this.nieBazyTable = [];
    this.T = 0;
  }

  setTable = table => {
    this.Tabela = table;
    // Filling Kolumna/Podaż table with last column from this.table table
    // Uzupełnianie Tabeli Podaż ostatnią columną z tabeli wprowadzonej do programu
    for (let i = 0; i < this.Tabela.length - 1; i++) {
      this.Podaz.push(this.Tabela[i][this.Tabela[i].length - 1]);
    }

    // filling Wiersz/Popyt table with last row from this.table table
    // Uzupełnianie Tabeli Popyt ostatnim wierszem z tabeli wprowadzonej do programu
    for (let i = 0; i < this.Tabela[this.Tabela.length - 1].length - 1; i++) {
      this.Popyt.push(this.Tabela[this.Tabela.length - 1][i]);
    }

    let sumInRow = 0; // Popyt
    let sumInCol = 0; // Podaż

    this.Popyt.forEach(e => (sumInRow += e));
    this.Podaz.forEach(e => (sumInCol += e));

    if (sumInCol !== sumInRow) {
      let diff = Math.abs(sumInCol - sumInRow);
      if (sumInRow < sumInCol) {
        this.Popyt.push(diff);
        this.Tabela.forEach((e, index) => {
          if (index === this.Tabela.length - 1) e.splice(e.length - 1, 0, diff);
          else e.splice(e.length - 1, 0, 0);
        });
      } else if (sumInRow > sumInCol) {
        let temptable = this.Tabela[0].map((e, index) =>
          index === this.Tabela[0].length - 1 ? diff : 0
        );
        this.Tabela.splice(this.Tabela.length - 1, 0, temptable);
        this.Podaz.push(diff);
      }
    }

    // Uzupełnianie tabeli Kosztów
    this.Koszty = [];
    for (let i = 0; i < this.Tabela.length - 1; i++) {
      let row = [];
      for (let j = 0; j < this.Tabela[i].length - 1; j++)
        row.push(this.Tabela[i][j]);
      this.Koszty.push(row);
    }
  };

  // 1. Metoda kąta północno-zachodniego

  katPolnocnoZachodni = () => {
    let wynik = this.Koszty.map(wiersz => wiersz.map(() => null));
    let podaz = this.Podaz.map(elem => elem);
    let popyt = this.Popyt.map(elem => elem);

    wynik.forEach((linia, x) =>
      linia.forEach((element, y) => {
        if (wynik[x][y] == null)
          if (podaz[x] !== 0 && popyt[y] !== 0) {
            let wartosc = podaz[x] > popyt[y] ? popyt[y] : podaz[x];
            wynik[x][y] = wartosc;
            podaz[x] -= wartosc;
            popyt[y] -= wartosc;
          }
        if (podaz[x] === 0 && popyt[y] === 0) {
          if (x < wynik.length - 1) wynik[x + 1][y] = 0;
          else if (y < wynik[x].length - 1) wynik[x][y + 1] = 0;
        }
      })
    );
    this.Rozwiazanie = wynik;
  };

  // 2. Metoda Potencjału dla bazowych

  bazowePotencjaly = () => {
    let Y = this.Podaz.map(() => null);
    let X = this.Popyt.map(() => null);

    Y[0] = 0;
    let flag = false;
    while (this.hasEmpty(Y) || this.hasEmpty(X)) {
      flag = false;
      for (let y = 0; y <= this.Rozwiazanie.length; y++) {
        for (let x = 0; x <= this.Rozwiazanie[y].length; x++) {
          if (this.Rozwiazanie[y][x] != null) {
            if (X[x] != null ? Y[y] == null : Y[y] != null) {
              if (X[x] == null) X[x] = this.Koszty[y][x] - Y[y];
              else if (Y[y] == null) Y[y] = this.Koszty[y][x] - X[x];
              flag = true;
              break;
            }
          }
        }
        if (flag) break;
      }
    }
    this.nieBazy.Y = Y.map(e => e);
    this.nieBazy.X = X.map(e => e);
    return { Y, X };
  };

  // 3. Metoda Potencjału dla kratek nie bazowych
  nieBazowePotencjaly = () => {
    const { X, Y } = this.return;
    let potencjaly = this.Koszty.map(elem => elem.map(e => e));

    this.Rozwiazanie.forEach((wiersz, y) =>
      wiersz.forEach((elem, x) => {
        if (this.Rozwiazanie[y][x] == null) {
          potencjaly[y][x] = X[x] + Y[y];
        }
      })
    );
    return potencjaly;
  };

  // 3. Sprawdzenie optymalności netodą potencjałów
  czyOptymalne = () => {
    let jestDodatnie = false;
    let potencjaly = this.return;
    potencjaly.forEach((wiersz, y) =>
      wiersz.forEach((elem, x) => {
        potencjaly[y][x] = potencjaly[y][x] - this.Koszty[y][x];
        if (potencjaly[y][x] > 0) jestDodatnie = true;
      })
    );
    if (!jestDodatnie) {
      this.Optymalne = true;
      return true;
    } else {
      return false;
    }
  };

  najDodatnia = potencjaly => {
    let maxVal = 0;
    let max = [];
    let newMax;
    let start;
    potencjaly.forEach((wiersz, y) => {
      wiersz.forEach((elem, x) => {
        if (elem === maxVal) max.push({ y, x });
        else if (elem > maxVal) {
          max = [];
          maxVal = elem;
          max.push({ y, x });
        }
      });
    });
    if (max.length === 1) {
      start = max[0];
    } else {
      let minKoszt = max[0].koszt;
      newMax = max[0];
      max.forEach(e => {
        if (e.koszt < minKoszt) newMax = e;
      });
      start = newMax;
    }
    return start;
  };

  cykl = () => {
    let start = this.return;
    this.Rozwiazanie[start.y][start.x] = 0;
    this.Cykl = [];
    this.cyklRoz = false;
    for (let i = 0; i < this.Koszty[start.y].length; i++) {
      if (
        this.Rozwiazanie[start.y][i] != null &&
        i !== start.x &&
        !this.cyklRoz
      )
        this.path(
          start,
          { y: start.y, x: i },
          this.Koszty.length + this.Koszty[start.y].length - 1,
          "col",
          start
        );
    }
  };

  hasInRow = elem => {
    let flag = false;
    for (let x = 0; x < this.Rozwiazanie[elem.y].length; x++) {
      if (this.Rozwiazanie[elem.y][x] != null) {
        flag = true;
      }
    }
    return flag;
  };

  hasInCol = elem => {
    let flag = false;
    for (let y = 0; y < this.Rozwiazanie.length; y++) {
      if (this.Rozwiazanie[y][elem.x] != null) {
        flag = true;
      }
    }
    return flag;
  };

  path = (start, actual, depth, dir, prev) => {
    let path = [];
    if (actual.y === start.y && actual.x === start.x) {
      this.Cykl.push({
        y: actual.y,
        x: actual.x,
        value: this.Rozwiazanie[actual.y][actual.x]
      });
      this.cyklRoz = true;
      return [prev];
    }
    if (depth < 0) return null;
    else if (!this.cyklRoz) {
      if (dir === "col") {
        if (this.hasInCol(actual)) {
          for (let i = 0; i < this.Rozwiazanie.length; i++) {
            if (this.Rozwiazanie[i][actual.x] != null && i !== actual.y)
              path.push(
                this.path(
                  start,
                  { x: actual.x, y: i },
                  depth - 1,
                  "row",
                  actual
                )
              );
          }
        }
      } else if (dir === "row") {
        if (this.hasInRow(actual)) {
          for (let i = 0; i < this.Rozwiazanie[actual.y].length; i++) {
            if (this.Rozwiazanie[actual.y][i] != null && i !== actual.x)
              path.push(
                this.path(
                  start,
                  { x: i, y: actual.y },
                  depth - 1,
                  "col",
                  actual
                )
              );
          }
        }
      }
      if (this.cyklRoz) {
        this.Cykl.push({
          y: actual.y,
          x: actual.x,
          value: this.Rozwiazanie[actual.y][actual.x]
        });
      }
      return path;
    }
  };

  elementMinimalnyCyklu = () => {
    let min = { x: undefined, y: undefined, value: Infinity };
    for (let i = 0; i < this.Cykl.length; i++) {
      if (i % 2 === 1) {
        if (
          min.value > this.Cykl[i].value ||
          (min.value === this.Cykl[i].value &&
            this.Koszty[min.y][min.x] <
              this.Koszty[this.Cykl[i].y][this.Cykl[i].x])
        )
          min = {
            x: this.Cykl[i].x,
            y: this.Cykl[i].y,
            value: this.Cykl[i].value
          };
      }
    }
    return min;
  };

  moveTValue = () => {
    let min = this.elementMinimalnyCyklu();

    for (let i = 0; i < this.Cykl.length; i++) {
      if (i % 2 === 0)
        this.Rozwiazanie[this.Cykl[i].y][this.Cykl[i].x] += min.value;
      if (i % 2 === 1)
        this.Rozwiazanie[this.Cykl[i].y][this.Cykl[i].x] -= min.value;
    }
    this.Rozwiazanie[min.y][min.x] = null;
  };

  hasEmpty = table => {
    for (let i = 0; i < table.length; i++) {
      if (table[i] == null) return true;
    }
    return false;
  };

  getRozwiazanie = () => {
    let table = this.Tabela.map(row => row.map(e => e));
    for (let i = 0; i < this.Rozwiazanie.length; i++)
      for (let j = 0; j < this.Rozwiazanie[i].length; j++)
        table[i][j] = this.Rozwiazanie[i][j];

    return table;
  };

  getNieBazowe = () => {
    let table = this.Tabela.map(row => row.map(e => e));
    for (let i = 0; i < this.return.length; i++)
      for (let j = 0; j < this.return[i].length; j++)
        if (this.Rozwiazanie[i][j] === null)
          table[i][j] = this.return[i][j] - this.Koszty[i][j];
        else table[i][j] = null;

    for (let i = 0; i < this.nieBazy.Y.length; i++)
      table[i][table[i].length - 1] = this.nieBazy.Y[i];

    for (let i = 0; i < this.nieBazy.X.length; i++)
      table[table.length - 1][i] = this.nieBazy.X[i];
    this.nieBazyTable = table.map(e => e.map(ee => ee));
    return table;
  };

  getBazowe = () => {
    let table = this.Tabela.map(row => row.map(e => e));
    for (let i = 0; i < table.length - 1; i++)
      for (let j = 0; j < table[i].length - 1; j++)
        if (this.Rozwiazanie[i][j] !== null) table[i][j] = this.Koszty[i][j];
        else table[i][j] = null;
    for (let i = 0; i < this.return.Y.length; i++)
      table[i][table[i].length - 1] = this.return.Y[i];

    for (let i = 0; i < this.return.X.length; i++)
      table[table.length - 1][i] = this.return.X[i];

    return table;
  };

  kolejnyKrok = () => {
    switch (this.next) {
      case 0:
        for (let i = 0; i < this.Rozwiazanie.length; i++) {
          for (let j = 0; j < this.Rozwiazanie[i].length; j++) {
            this.kosztOptymalny += this.Rozwiazanie[i][j] * this.Koszty[i][j];
          }
        }
        return {
          data: this.getRozwiazanie(),
          caseIndex: 0,
          desc: "Optymalne"
        };
      case 1:
        this.katPolnocnoZachodni();
        this.next = 2;
        return {
          data: this.getRozwiazanie(),
          caseIndex: 1,
          desc: "kąt północno zachodni"
        };
      case 2:
        this.return = this.bazowePotencjaly();
        this.next = 3;
        return {
          data: this.getBazowe(),
          caseIndex: 2,
          desc: "obliczam Potencjały dla bazowych"
        };

      case 3:
        this.return = this.nieBazowePotencjaly();
        this.next = 4;
        return {
          data: this.getNieBazowe(),
          caseIndex: 3,
          desc: "obliczam potencjały dla nie bazowych"
        };
      case 4:
        if (this.czyOptymalne()) {
          this.next = 0;
          return this.kolejnyKrok();
        } else {
          this.next = 5;
          return {
            data: this.kolejnyKrok(),
            caseIndex: 5,
            desc: "nie optymalne"
          };
        }
      case 5:
        this.return = this.najDodatnia(this.return);
        this.next = 6;
        return this.return;

      case 6:
        this.cykl();
        this.next = 7;
        this.T = this.elementMinimalnyCyklu();
        return {
          data: {
            cykl: this.Cykl,
            t: this.T
          },
          caseIndex: 6,
          desc: "tworzę cykl po kratkach nie bazowych"
        };
      case 7:
        this.moveTValue();
        this.next = 2;
        return {
          data: this.getRozwiazanie(),
          caseIndex: 7,
          desc: "Przemieszczam wartość t po cyklu"
        };
      default:
        break;
    }
  };
}

export const zt = new ZT();
