import React, { useState } from "react";
import Table from "./Table";
import { Link } from "react-router-dom";

const Steps = ({ zt, start }) => {
  const [steps, setSteps] = useState([
    <Table key={0} table={zt.Tabela} title={"Tablela kosztów"}></Table>
  ]);
  const handleAddNext = () => {
    if (!zt.Optymalne) {
      let step = zt.kolejnyKrok();
      if (
        step.caseIndex === 0 ||
        step.caseIndex === 1 ||
        step.caseIndex === 2 ||
        step.caseIndex === 3
      ) {
        let tab = (
          <Table key={steps.length} table={step.data} title={step.desc}></Table>
        );
        setSteps([...steps, tab]);
      } else if (step.caseIndex === 5) {
        let tab = (
          <Table
            cell={step.data}
            key={steps.length}
            table={zt.nieBazyTable}
            title={step.desc}
          ></Table>
        );
        setSteps([...steps, tab]);
      } else if (step.caseIndex === 6) {
        let table = zt.getRozwiazanie().map(e => e.map(ee => ee));
        for (let i = 0; i < step.data.cykl.length; i++) {
          if (table[step.data.cykl[i].y][step.data.cykl[i].x] !== null)
            table[step.data.cykl[i].y][step.data.cykl[i].x] +=
              i % 2 === 0 ? " +t" : " -t";
          else
            table[step.data.cykl[i].y][step.data.cykl[i].x] =
              i % 2 === 0 ? " 0 +t" : "0 -t";
        }
        console.log(step);

        let tab = (
          <Table
            cell={step.data}
            key={steps.length}
            table={table}
            title={step.desc}
          ></Table>
        );
        setSteps([...steps, tab]);
      }
      if (step.caseIndex === 7) {
        let tab = (
          <Table
            key={steps.length}
            table={step.data}
            title={`Przemieszczam wartość t= ${zt.T.value} po cyklu`}
          ></Table>
        );
        setSteps([...steps, tab]);
      }
      if (step.caseIndex === 0) {
        let tab = <h2 key="koszt">Koszt optymalny to: {zt.kosztOptymalny}</h2>;
        let tab2 = (
          <Table
            key={steps.length}
            table={zt.getRozwiazanie()}
            title={`Rozwiązanie`}
            titleStyle={{ color: "green" }}
          ></Table>
        );
        setSteps([...steps, tab2, tab]);
      }
    }
  };

  return (
    <>
      <div className="btnWrap">
        {!zt.Optymalne ? (
          <button className="btnNext" onClick={handleAddNext}>
            NASTĘPNY
          </button>
        ) : (
          <></>
        )}
      </div>
      {steps}

      <Link to="/">POWRÓT</Link>
    </>
  );
};

export default Steps;
