import React from "react";
import "../CSS/Table.css";
const Table = ({
  table,
  title,
  titleStyle = {},
  styles = {},
  cell = { x: null, y: null }
}) => {
  let tabViev = [];
  if (table.length > 0)
    for (let i = 0; i < table.length; i++) {
      let row = [];
      for (let j = 0; j < table[i].length; j++) {
        let styless =
          i === table.length - 1 && j === table[i].length - 1
            ? { fontSize: 0, backgroundColor: "#35ADC0" }
            : i === table.length - 1 || j === table[i].length - 1
            ? { backgroundColor: "#35ADC0" }
            : {};

        let styleCell = i === cell.y && j === cell.x ? { color: "red" } : {};

        row.push(
          <div
            key={j}
            className="tableCell"
            style={{ ...styless, ...styles, ...styleCell }}
          >
            {table[i][j] != null ? table[i][j] : " "}
          </div>
        );
      }

      tabViev.push(
        <div key={i} className="tableRow">
          {" "}
          {row}
        </div>
      );
    }
  return (
    <>
      <h3 className="tableTitle" style={titleStyle}>
        {title}
      </h3>
      <div className="tableBorder">
        {table.length !== undefined ? tabViev : <div></div>}
      </div>
    </>
  );
};

export default Table;
